.App {
  text-align: center;


}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #2d2d2d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(6px + .9vmin);
  color: #202020 ;
  text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
          letter-spacing: .1em;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.box{
  position:relative;
  width: 70px;
  height: 70px;
  transition: all .5s;
}
.item{
  position: absolute;
  right: 0;

  text-align: right;
  line-height: 2rem;
  opacity: 100;
  transition: all .5s;
  font-size: 1.0rem;
height: 50px;
width: 50px;
  top: 1rem;
  bottom: 1rem;



}
.item.active{



  opacity: 1;
  font-size:1.0rem;
      color: #fff;
  text-shadow:
    0 0 5px #fff,
    0 0 10px #fff,
    0 0 20px #fff,
    0 0 40px #0ff,
    0 0 80px #0ff,
    0 0 90px #0ff,
    0 0 100px #0ff,
    0 0 150px #0ff;
}

.month{
  width: 200px;
  right: -100px;
}


.day{
  width: 480px;
  right: -240px;
}

.week{
  width: 360px;
  right: -180px;
}

.hour{
  width: 620px;
  right: -308px;
}
.Minute{
  width: 800px;
  right: -400px;
}
.second{
  width: 950px;
  right: -475px;
}



.msg{
  position: absolute;
  left: 2rem;
  top: 2rem;
  text-align: left;
  line-height: 2rem;
  width: 300px;
}
.msg div{
  opacity: .8;
}
.msg .year{
  margin-bottom: 1rem;
  opacity: 10;
    margin-left: 6px;
    color: #202020;
    background-color: #2d2d2d;
    letter-spacing: .1em;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;

}
.msg .year span{
  /* display: block; */
  font-size: 1.5rem;
  padding-left: 4px;
  padding-top: 4px;


}
.M_month{
  margin-bottom: 1rem;
  opacity: 1;

}


.glitch {

  font-size: .7rem;
padding-left: 10px;
  padding-top: 1px;


}